* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

body,
button {
	font-family: 'NevisBold', sans-serif;
}

h1,
h2,
li {
	font-family: 'NevisBold', sans-serif !important;
}

html {
	scroll-behavior: smooth;
	scroll-padding-top: 150px;
}

body {
	height: 100%;
	position: relative;
}

.App {
	min-height: 100vh;
	position: relative;
}

p {
	letter-spacing: 1px;
	line-height: 1.5;
}

@media screen and (max-width: 960px) {
	html {
		scroll-padding-top: 100px;
	}
}
